.project {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 40px;
}

.imgContainer {
    width: 100%;
    height: 300px;
    background-color: #664141;
    position: relative;
}

.imgContainer h3  {
    position: absolute;
    bottom: 40px;
    left: 40px;
    font-size: 34px;
    font-weight: 300;
}

.imgContainer h3::after {
    content: '';
    background-color: #fff;

    position: relative;
    bottom: -10px;
    left: 0;

    display: block;
    width: 40px;
    height: 1px;
}